const idPattern = /^[a-z0-9]{5,15}$/;
const pwPattern = /^(?=.*?[0-9])(?=.*?[#?!@$ %^&*-]).{5,15}$/
const namePattern = /^[Í∞Ä-Ìû£]{2,}$/;
const idcardPattern = /[0-9]{6,}/;

const equlChk = document.getElementById('user_pw_chk');
